* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.wrapper {
  max-width: 1000px;
  margin: 0 auto;
  box-sizing: border-box;
}
.btn {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  border-radius: 0px 15px 15px 0px;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;

  padding: 12px;
  &:hover {
    background-color: rgba(0, 0, 0, 0.8);
  }
}
.App {
  width: 100%;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;

  background-size: cover;
  .search {
    padding: 20px;
    text-align: center;
    input {
      max-width: 200px;
      height: 40px;

      border-radius: 15px 0px 0px 15px;
      padding: 0 18px;
      font-size: 16px;
      outline: none;
      border: none;
    }
  }
}

.weather {
  .city_name_weather {
    color: #fff;
    font-size: 40px;
    flex-wrap: wrap;
    text-align: center;
  }
  .block_main_information {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .temreture {
      width: 300px;
      padding: 10px;
      background-color: rgba(0, 0, 0, 0.4);
      backdrop-filter: blur(2.5px);
      display: flex;
      flex-direction: column;
      border-radius: 20px;
      gap: 30px;
      .info {
        display: flex;
        justify-content: space-around;
        padding: 10px 4px;
        border-radius: 20px;
        background-color: rgba(255, 255, 255, 0.4);
        margin-bottom: auto;
        p {
          display: flex;
          flex-direction: column;
          align-items: center;
        }
      }
      .temperature_value {
        .town {
          font-size: 50px;
          font-weight: bold;
          color: white;
          text-shadow: 0px 0px 20px #fff;
          padding: 20px 0;
          .red {
            background-color: red;
          }
          .green {
            background-color: green;
          }
        }
        font-size: 40px;
        color: white;
        text-align: center;
        font-weight: 700;
      }
    }
  }
}

.error {
  background-color: rgba(0, 0, 0, 0.9);
  border-radius: 40px;
  max-width: 400px;
  font-size: calc(12px + 1vmin);
  color: white;
  display: table;
  padding: 10px 20px;
  margin: 0 auto;
}

.red {
  color: red;
}
.green {
  color: green;
}
.img{
  display: flex;
  justify-content: center;
  align-items: center;
  p{
    font-size: 15px;
  }
}